import React, { Component } from 'react';
import loading from '../../../../src/loading.gif';
import { Link } from "react-router-dom";
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';


class AuthorConvoWith extends Component {
    constructor(props)
    {
        super(props);
        this.state={
        }
    }
    render() {
        console.log("Props are ",this.props)
        return (
            <div>
                {this.props.sortedAuthorConvoWithAuthors && this.props.sortedAuthorConvoWithAuthors.length > 0 && <div>
                    <div className="dytb">
                        <i style={{ cursor: "pointer" }} onClick={() => this.props.reverse2()} className="fa fa-angle-left dytbc" />
                        {this.props.sortedAuthorConvoWithAuthors.map(author => {

                            return (
                                <div onClick={() => this.props.onAuthorClick(author)} style={{ marginRight: "8px", display: "inline-block", marginLeft: "8px", width: "85px", maxHeight: "180px", border: "1px solid white", borderRadius: "3px", cursor: "pointer" }}>
                                    <div style={{ width: "83px", minHeight: "70px", backgroundColor: "yellow" }}>
                                        <img src={`https://conceptionary-images.s3.amazonaws.com/${author.lastName}+${author.firstName}.jpg`} alt={author.firstName + " " + author.lastName}></img>
                                    </div>
                                    <div style={{ fontSize: "13px", width: "83px", overflow: "hidden", textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>{author.firstName + author.lastName}</div>
                                </div>
                            );
                        })}
                        <i style={{ cursor: "pointer" }} onClick={() => this.props.forward2()} className="fa fa-angle-right dytbc"></i>
                    </div>
                </div>
                }
            </div>
  
      );
    }
}
export default AuthorConvoWith