import React, { Component } from 'react';
import ReactDOM from 'react-dom';


class KeywordsSidebar extends Component {

    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className='' id='firstInnerScr' style={{ width: "100%", margin: "auto", background: "white", textAlign: '', borderRadius: '20px' }}>
                {/* {this.props.listRelatedLoading && <div style={{ width: "100px", margin: "auto" }}>
                    <ReactLoading type="balls" color="red" />
                </div>} */}
                {<div style={{ width: "100%", margin: "auto" }} >
                    <div style={{ backgroundColor: 'white', width: '100%', paddingTop: "50px" }}>
                        <div className="row">
                            <div className="col-xs-2 col-sm-2 col-md-2" style={{ backgroundColor: "white", cursor: 'pointer', }}>
                              {
                                  this.props.keywords.length>0 &&  <div onClick={this.props.showAllClicked} className="btn btn-link text-center rotate" style={{ position: 'fixed', top: '30%' }}  >
                                    <b style={{ fontSize: 16 }}>Show All</b>
                                  </div> 
                               }
                            </div>
                            <div id='favourite' className="col-8 col-sm-8 col-md-8" style={{ backgroundColor: 'white', paddingLeft: "30px" }}>
                                <ul style={{ "lineHeight": "0.9em", height: "auto", listStyle: "none", padding: 0 }}>
                                    {this.props.keywords.map((keyword, index) => {
                                        return (
                                            <li id={keyword.id} style={{ padding: "8px" }}>
                                                <a href="#top" style={{ cursor: "pointer", textDecoration: "none", color: "black", display: "block" }} onClick={() => { this.props.onKeywordClick(keyword) }}>
                                                    <b style={{ color: 'black', fontWeight: '15px:', fontSize: '14px' }}>{keyword.name}</b>&nbsp;
                                                <span style={{ color: 'gray', fontSize: 11 }}>
                                                        {" (" + keyword.count + ")"}
                                                    </span>
                                                </a>
                                            </li>);

                                    })}
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>}
            </div>
        );
    }

}

export default KeywordsSidebar;