import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import "./ConceptsPage.css";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { withRouter } from 'react-router-dom';
import ReactLoading from 'react-loading';
import AdsComponent from '../AdComponent/AdComponent';
import loading from '../../../src/loading.gif';
import $ from "jquery";
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';

import {
    flushConceptState, getListRelatedByConceptId,
    getConceptFromApi,
    increasePerspectiveLike,
    decreasePerspectiveLike,
    getConceptPerspectivesByLikes,
    getConceptPerspectivesByKeywords,
    getPerspectivesByAuthors,
    getConceptPerspectivesByAuthorLastName,
} from "../../actions/conceptsActions";

import { setSelectedItem, addTag } from "../../actions/navbaractions";
import { getAuthorGroups } from "../../utilities";
import Context from "./Context/Context";
import { getFilteredPerspectives, getUniqueKeywords, getUniqueSortedConceptsFromConceptCluster } from '../../selectors';
import queryString from "query-string";
import Axios from 'axios';
import KeywordsSidebar from './KeywordsSidebar/KeywordsSidebar';
import AuthorsSidebar from './AuthorsSidebar/AuthorsSidebar';
import Header from './Header/Header';
import { Link, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'
import JQuery from 'jquery'


class ConceptsPage extends Component {

    showAuthors = false
    sortedByKewords = false
    allowDownScrollCallBack = false;
    keywordsdlistRelateIndex = 0
    updateState = true;
    authorLoading = false

    prevScroll = 0
    prevFirstScroll = 0;
    prevThirdScroll = 0
    ignoreWindowScrollEvents = false
    ignoreFirstScrollEvents = false
    ignoreThirdScrollEvents = false

    constructor(props) {

        super(props);
        this.state = {
            query: "",
            showAuthors: false,
            filters: false,
            listRelated: true,
            authorId: null,
            id: "",
            sortByAuthor: null,
            perspectiveLoading: false,
            sortByLikes: false,
            prevOffset: 1000,
            keywordsClicked: false,
            viewport: "",
            leftDropdownOpen: false,
            rightDropdownOpen: false,
            keywordApiLoading: false,
            autherApiLoading: false,
            filteredPerspectives:[]


        };

        if (this.props.navbar.selectedOption && this.props.navbar.selectedOption.id && this.props.navbar.selectedOption.category === "Concepts") {
            let concept = {
                "concept_id": this.props.navbar.selectedOption.id,
                "filters": this.props.navbar.tags.map(tag => tag.id),
            };
            this.props.getConceptFromApi(concept);
            this.props.getListRelatedByConceptId(concept)

        }
        this.handleScrollCallback = this.handleScrollCallback.bind(this)
        this.handleFirstWheeel = this.handleFirstWheeel.bind(this)
        this.handleThirdWheeel=this.handleThirdWheeel.bind(this)
        this.handleBodyWheel=this.handleBodyWheel.bind(this)

    }
    shouldComponentUpdate() {
        // if (!this.updateState) {
        //     this.updateState = true
        //     return false
        // }
        // else
        return true
    }
    showAll = () => {

        this.sortedByKewords = true
        this.setState({
            sortBy: "keyword",
            keywordApiLoading: false,
            sortByKeywordId: null,
            sortByAuthor: null,
            keywordsClicked: true,
            viewport: "",
            upScrollFilterLock: true, apiLoading: true
        }, () => {
            $(window).scrollTop(0);
            console.log(this.props.concepts.keywords)
            let concepts = this.props.uniqueKeywords.slice(0, 10);

            let UpConcepts = []

            let concate = UpConcepts.concat(concepts);

            this.keywordsdIndex += 10
            let conceptIds = concate.map(concept => concept.id)
            this.props.getConceptPerspectivesByKeywords(conceptIds)
        })

    }
    onListRelatedClick = (listRelated) => {
        let label = listRelated.relationary_supra;
        let value = label;
        let id = listRelated.relationary_thing_supra_id;
        let category = "Concept-Clusters";
        this.props.flushConceptState()
        this.props.setSelectedItem({ label, value, id, category });
    }
    getConcept = (concept_name) => {
        return new Promise((resolve, reject) => {
            Axios.get(`${process.env.REACT_APP_SERVER}/api/rest/v1/concepts?name=${concept_name}`).then(data => {
                if (data.data.obj) {
                    let result = data.data.obj.selectedOption;
                    resolve(result);
                }
                resolve(null);
            }).catch(err => {
                console.log(err);
            });
        });
    }
    async componentWillMount() {
        if (this.props.match.params.name) {
            if (this.props.match.params.name && queryString.parse(this.props.location.search).filters) {
                let filters = [];
                let queryFilters = queryString.parse(this.props.location.search).filters;
                let concept_name = this.props.match.params.name;
                if (typeof queryFilters === "string") {
                    filters.push(queryFilters);
                } else {
                    filters = queryFilters;
                }
                this.setState({
                    query: concept_name,
                    filters: true
                })
                let result = await this.getConcept(concept_name);
                let author_group = await getAuthorGroups(filters);
                if (result) {
                    this.props.addTag(author_group);
                    this.props.setSelectedItem(result);
                    this.setState({
                        selectedOption: result,
                    });
                } else {
                    this.props.history.push('/Message');
                }

            } else {
                let concept_name = this.props.match.params.name;
                this.setState({
                    query: concept_name
                });
                let result = await this.getConcept(concept_name);
                if (result) {
                    this.props.setSelectedItem(result);
                    this.setState({
                        selectedOption: result,
                    })
                } else {
                    this.props.history.push('/Message');
                }
            }
        }

    }
    componentWillReceiveProps(nextProps) {
        console.log("will receive props",nextProps.filteredPerspectives)
        console.log("before",this.state.filteredPerspectives)
        this.setState({ 
            perspectiveLoading: false,
            filteredPerspectives:nextProps.filteredPerspectives
        })
        
        if (this.props.navbar.selectedOption != nextProps.navbar.selectedOption) {

            this.showAuthors = false
            this.sortedByKewords = false
            this.allowDownScrollCallBack = false;
            this.keywordsdIndex = 0

            this.setState({
                perspectiveLoading: false,
                offset: 0,
                sortByLikes: false,
                prevOffset: 1000,
                keywordsClicked: false,
                viewport: "",
                leftDropdownOpen: false,
                rightDropdownOpen: false,
            })
            this.props.flushConceptState();

            if (nextProps.navbar.selectedOption.id && nextProps.navbar.selectedOption.category == "Concepts") {
                this.setState({
                });

                let concept = {
                    "concept_id": nextProps.navbar.selectedOption.id,
                    "filters": nextProps.navbar.tags.map(tag => tag.id)
                };
                this.props.getConceptFromApi(concept)
                this.props.getListRelatedByConceptId(concept);
            }
        }
        if (this.props.navbar.tags.length !== nextProps.navbar.tags.length) {
            if (nextProps.navbar.selectedOption.category == "Concepts") {
                let concept = {
                    "concept_id": nextProps.navbar.selectedOption.id,
                    "filters": nextProps.navbar.tags.map(tag => tag.id)
                };
                this.props.getConceptFromApi(concept)
                this.props.getListRelatedByConceptId(concept);
            }

        }
        if (nextProps.concepts.sortedAuthorIds && nextProps.concepts.sortedAuthorIds.length > 0 && nextProps.concepts.perspectivesByAuthor) {
            let sortedAuthorIds = nextProps.concepts.sortedAuthorIds;
            if (!nextProps.concepts.initial) {
                let authors = sortedAuthorIds.slice(0, 10);
                if (nextProps.isScrollLike) {
                    this.props.getConceptPerspectivesByLikes(this.props.navbar.selectedOption, 0, false);
                    this.showAuthors = false
                }
                else {
                    this.showAuthors = true
                    this.props.getPerspectivesByAuthors(authors, this.props.navbar.selectedOption.id);
                }



            }
        }
        if (nextProps.concepts.perspectivesByAuthor !== this.props.filteredPerspectives) {
            this.setState({
                perspectiveLoading: false,
            
            })
        }
        if (nextProps.sortByLikes !== this.props.sortByLikes) {
            this.setState({
                sortByLikes: nextProps.sortByLikes
            })
        }
    }
    componentDidUpdate() {
        console.log("set view port to ", this.state.viewport);
        const element = document.getElementById(this.state.viewport);
        if (element) {
            console.log('i found this' + this.state.viewport)
            element.scrollIntoView(true);
            $(window).scrollTop($(window).scrollTop() -165)
        }

        this.prevScroll = $(window).scrollTop()

    }
    handleBodyWheel(x)
    {
        console.log(x.originalEvent.deltaY)
        let firstScroll = $('#firstScr').scrollTop()
        let secondScroll = $(window).scrollTop()
        let thirdScroll = $('#thirdScr').scrollTop();
        console.log(secondScroll)
        console.log($('#favouriteSecond').height())
        if(secondScroll==0 || secondScroll>=$('#favouriteSecond').height())
        {
            // alert('hello')
            this.ignoreFirstScrollEvents = true
            this.ignoreThirdScrollEvents=true
            console.log("setting first",firstScroll +x.originalEvent.deltaY)
            $('#firstScr').scrollTop(firstScroll +x.originalEvent.deltaY )
            $('#thirdScr').scrollTop($('#thirdScr').scrollTop() +x.originalEvent.deltaY)
            this.prevScroll = secondScroll;
            this.prevFirstScroll = firstScroll;
            this.prevThirdScroll = thirdScroll;

        }

    }
    handleFirstWheeel(x) {
        console.log("wheeling first")
        if(!this.ignoreFirstScrollEvents)
        {
            
            let firstScroll = $('#firstScr').scrollTop()
            let secondScroll = $(window).scrollTop()
            let thirdScroll = $('#thirdScr').scrollTop();
            let diff = firstScroll - this.prevFirstScroll;
            this.ignoreWindowScrollEvents = true
            this.ignoreThirdScrollEvents=true
            $(window).scrollTop(secondScroll + diff)
            $('#thirdScr').scrollTop($('#thirdScr').scrollTop() + diff)
            this.prevScroll = secondScroll;
            this.prevFirstScroll = firstScroll;
            this.prevThirdScroll = thirdScroll;

        } 
        this.ignoreFirstScrollEvents=false;

    }

    handleScrollCallback() {


        console.log("body scroll",this.ignoreWindowScrollEvents)
        let oldScroll = this.prevScroll;
           
        if(!this.ignoreWindowScrollEvents)
        {
            let firstScroll = $('#firstScr').scrollTop()
            let secondScroll = $(window).scrollTop()
            let thirdScroll = $('#thirdScr').scrollTop();
            // console.log("whelling")
            let diff = secondScroll - this.prevScroll;
            console.log("difference is ", diff)
            // console.log("sending", this.ignoreWindowScrollEvents)
            this.ignoreFirstScrollEvents = true
            this.ignoreThirdScrollEvents=true
            $('#firstScr').scrollTop(firstScroll + diff)
            $('#thirdScr').scrollTop($('#thirdScr').scrollTop() + diff)
            this.prevScroll = secondScroll;
            this.prevFirstScroll = firstScroll;
            this.prevThirdScroll = thirdScroll;

        }
        this.ignoreWindowScrollEvents=false;
        
        if ($(window).scrollTop() > oldScroll) {
            //Down Scrolling
            this.handleDown();
        }
        else {
            //Up Scrolling
            this.handleUp()
        }

    }
    handleThirdWheeel(x)
    {
        console.log("Third scroll",this.ignoreThirdScrollEvents)
        if(!this.ignoreThirdScrollEvents)
        {
                console.log("inside")

                let firstScroll = $('#firstScr').scrollTop()
                let secondScroll = $(window).scrollTop()
                let thirdScroll = $('#thirdScr').scrollTop();
                let diff = thirdScroll - this.prevThirdScroll;
                console.log("diff is ",thirdScroll)
                this.ignoreWindowScrollEvents = true
                this.ignoreFirstScrollEvents=true
                
                $(window).scrollTop(secondScroll + diff)
                $('#firstScr').scrollTop($('#firstScr').scrollTop() + diff)
                
                
                this.prevScroll = secondScroll;
                this.prevFirstScroll = firstScroll;
                this.prevThirdScroll = thirdScroll;
    
            

        } 
        this.ignoreThirdScrollEvents=false;

    }


    loadKeywordsDown = () => {

        var windowHeight = $(window).height();
        var scrollHeight = $(window).scrollTop();
        var rightBarHeight = $('#favouriteSecond').outerHeight();

        if ((windowHeight + scrollHeight) >= rightBarHeight) {
            let concepts = this.props.uniqueKeywords.slice(this.keywordsdIndex, this.keywordsdIndex + 6);
            this.keywordsdIndex += 6
            let conceptIds = concepts.map(concept => concept.id)
            this.setState({ viewport: "" })
            this.props.getConceptPerspectivesByKeywords(conceptIds, true)


        }

    }
    callApi = () => {
        var windowHeight = $(window).height();
        var scrollHeight = $(window).scrollTop();
        console.log("call api")
        if (this.state.keywordsClicked) {
            this.loadKeywordsDown();
        }
        if ((windowHeight + scrollHeight) >= $('#favouriteSecond').innerHeight()) {

            if (this.allowDownScrollCallBack) {
                this.allowDownScrollCallBack = false;
                if (!this.sortedByKewords) {
                    if (!this.props.concepts.finish && !this.state.perspectiveLoading) {
                        this.scrollDownPerspectiveHandler();

                    }
                }
            }



        }
        else {
            this.allowDownScrollCallBack = true;
        }
    }
    handleDown = () => {
        console.log("handle down")
        this.callApi()

    }
    handleUp = () => {
        if ($(window).scrollTop() == 0 && !this.authorLoading && !this.props.apiLoading) {
            if (!this.props.concepts.upLoading) {
                this.setState({
                    upScrollFilterLock: true,
                    upScrolling: true,
                    viewport: null
                })
                this.scrollUpPerspectiveHandler();
            }


        }


    }

    componentWillUnmount() {

        // Events.scrollEvent.remove('begin');
        // Events.scrollEvent.remove('end');
        $(window).off('scroll')
        // window.removeEventListener('scroll', this.handleScrollCallback, true);
    }

    componentDidMount() {
        // Events.scrollEvent.register('begin', function (to, element) {
        //     console.log("begin", arguments);
        // });

        // Events.scrollEvent.register('end', function (to, element) {
        //     console.log("end", arguments);
        // });

        // scrollSpy.update();

        // $(window).scrollTop(0);
        // window.addEventListener('scroll', this.handleScrollCallback, true);
    
        $(window).on('scroll',this.handleScrollCallback);
        $(window).on('wheel',this.handleBodyWheel)
    
    }

    toggleListRelated = () => {
        this.setState({
            listRelated: !this.state.listRelated,
        })
    }

    onLikeClick = (id) => {
        
        let index = this.state.filteredPerspectives.findIndex(x => x.id == id)
        let oldState = this.state.filteredPerspectives;
        if (this.state.filteredPerspectives[index].isLike) {
            this.props.decreasePerspectiveLike(id);
            oldState[index].loves = --oldState[index].loves;
        }
        else {
            this.props.increasePerspectiveLike(id);
            oldState[index].loves = ++oldState[index].loves;

        }

        oldState[index].isLike = !oldState[index].isLike;
        this.setState({filteredPerspectives:oldState})
    }
    onAuthorClick = (author) => {
        let label = author.author_first + " " + author.author_last;
        let value = label;
        let id = author.auth_id;
        let born = author.born;
        let died = author.died;
        let category = "Authors"
        this.props.flushConceptState()
        this.props.setSelectedItem({ label, value, id, born, died, category });
    }
    filterConceptDefinations = (keyword_id) => {
        let rtn = [];
        this.state.orignalConceptsWithDetails.forEach(element => {
            if (element.keywords_id == keyword_id) {
                rtn.push(element);
            }
        });
        this.setState({
            conceptsWithDetails: rtn,
        })
    }
    loadPerspectivesByLikes = () => {

        $(window).scrollTop(0);
        this.setState({ keywordsClicked: false, viewport: "", keywordApiLoading: false, sortByLikes: true })
        this.sortedByKewords = false
        this.keywordsClicked = false;
        this.props.getConceptPerspectivesByLikes(this.props.navbar.selectedOption, 0, false);

    }
    loadPerspectivesByAuthor = (lastName, i) => {
        // this.setState({authorLoading:true})
        this.authorLoading = true
        this.keywordsClicked = false

        let sortedAuthorIds = this.props.concepts.sortedAuthorIds;
        let perspectives = this.state.filteredPerspectives;
        let includes = false;
        for (let p of perspectives) {
            if (p.Author == this.state.authorId) {
                includes = true;
                break;
            }
        }
        let index = i
        let authors = [];
        let beginners = sortedAuthorIds.slice(index - 5, index);
        let last = sortedAuthorIds.slice(index + 1, index + 5)
        authors.push(...beginners, sortedAuthorIds[index], ...last);
        // authors.push( sortedAuthorIds[index], ...last);

        if (!this.perspectiveLoading) {
            this.props.getPerspectivesByAuthors(authors, this.props.navbar.selectedOption.id)
            this.setState({
                viewport: lastName
            })

        }

    }
    scrollDownPerspectiveHandler = () => {
        if (this.state.sortByLikes) {
            if (this.state.prevOffset !== this.props.offset) {
                this.setState({
                    perspectiveLoading: true,
                })
                this.props.getConceptPerspectivesByLikes(this.props.navbar.selectedOption, this.props.offset, true);
                this.setState({ calledapi: false })
                this.state.prevOffset = this.props.offset
            }
        }
        else {

            let sortedAuthorIds = this.props.concepts.sortedAuthorIds;
            let perspectives = this.state.filteredPerspectives;
            let index = sortedAuthorIds.indexOf(perspectives[perspectives.length - 1].Author)
            let authors = sortedAuthorIds.slice(index + 1, index + 10);
            if (authors.length > 0) {
                this.updateState = false;
                this.setState({
                    perspectiveLoading: true,
                    // sortByAuthor: this.state.authors[perspectives[perspectives.length - 1].Author].lastName,
                    sortByAuthor: null,
                    viewport: null
                })
                this.props.getPerspectivesByAuthors(authors, this.props.navbar.selectedOption.id, true);

            }
        }

    }
    scrollUpPerspectiveHandler = async () => {

        if (!this.state.sortByLikes) {
            if (this.state.filteredPerspectives) {

                let sortedAuthorIds = this.props.concepts.sortedAuthorIds;
                let perspectives = this.state.filteredPerspectives;
                let index = sortedAuthorIds.indexOf(perspectives[0].Author)
                if (index !== 0) {
                    let authors = [];
                    for (var i = 1; i < 5; i++) {
                        let value = sortedAuthorIds[index - i];
                        if (value !== undefined) {
                            authors.push(value)
                        }
                    }
                    if (authors.length > 0) {
                        this.setState({
                            sortByAuthor: this.props.concepts.authors[perspectives[0].Author].lastName,
                            perspectiveLoading: true,
                            viewport: this.props.concepts.authors[this.state.filteredPerspectives[0].AuthorId].lastName,
                            authorPrevScroll: 0,
                            autherApiLoading: true,
                        })
                        await this.props.getPerspectivesByAuthors(authors, this.props.navbar.selectedOption.id, true);

                    }
                    else {
                        this.setState({
                            sortByAuthor: this.state.authors[perspectives[0].Author].lastName,
                            perspectiveLoading: false,
                            viewport: this.state.authors[this.state.filteredPerspectives[0].AuthorId].lastName


                        })
                    }
                }
            }

        }


    }
    loadByAuthor = () => {

        this.setState({ keywordsClicked: false, keywordApiLoading: false })

        if (this.state.sortByLikes) {
            let sortedAuthorIds = this.props.concepts.sortedAuthorIds;
            let authors = sortedAuthorIds.slice(0, 30);
            this.props.getPerspectivesByAuthors(authors, this.props.navbar.selectedOption.id);
            this.showAuthors = true;
        }
        $(window).scrollTop(10);
    }

    toggleSortLeft = () => {
        this.setState(prevState => ({
            leftDropdownOpen: !prevState.leftDropdownOpen
        }))

    }
    toggleSortRight = () => {
        this.setState(prevState => ({
            rightDropdownOpen: !prevState.rightDropdownOpen
        }))
    }
    render() {
        if(this.state.filteredPerspectives)
        {
        console.log("before state is ",this.state.filteredPerspectives.length)
        }
        return (
            <div className="overflowhidden" style={{ overflow: "hidden", paddingTop: 50 }}>
                {(this.props.navbar.selectedOption.id && this.props.navbar.selectedOption.category == "Concepts" || this.props.query) && < div className="container-fluid">

                    {this.props.apiLoading && <div style={{ width: "100px", margin: "auto", marginTop: "30vh" }}>
                        <ReactLoading type={"balls"} color={"red"} />
                    </div>}
                    {!this.props.apiLoading && !this.props.apiNetworkError && !this.props.apiServerError && <div className="row">
                        <div ref="firstScroll" onScroll={this.handleFirstWheeel} id="firstScr" className="col-6 col-sm-6 col-md-3 right d-none d-md-block " style={{ paddingBottom: 50, borderRadius: "3px", backgroundColor: "white", textTransform: 'capitalize', overflow: 'auto', textAlign: "", height: '100vh', position: 'fixed', }}>
                            <KeywordsSidebar keywords={this.props.uniqueKeywords} showAllClicked={this.showAll} onKeywordClick={(keyword) => {

                                var scroll = 0;
                                this.sortedByKewords = true
                                // Ya attribute isss liya add kiya ha ta k script.js file ko pta lg sky k filhal sirf aik hi keyword ka data load ho rha ha aur phr wo secondColumn ko stop krny ka method call kr sky

                                this.setState({
                                    sortBy: "keyword",
                                    sortByKeywordId: keyword.id,
                                    sortByAuthor: null,
                                    keywordsClicked: false,
                                    apiLoading: true,
                                    keywordApiLoading: true,
                                    autherApiLoading: false,
                                    viewport:null
                                }, () => {
                                    this.ignoreWindowScrollEvents=true
                                    this.props.getConceptPerspectivesByKeywords(keyword.id)

                                })

                            }} />
                        </div>
                        <div ref="myscroll" id="secondScr" className="col-12 col-sm-12 col-md-4 left offset-md-3 " style={{ borderRadius: "3px", backgroundColor: "white" }}>
                            <div ref="myscrollInner" id="favouriteSecond" style={{ backgroundColor: "white" }}>
                                <Header
                                    leftDropdownOpen={this.state.leftDropdownOpen}
                                    listRelatedData={this.props.concepts.listRelatedData}
                                    selectedOption={this.props.navbar.selectedOption}
                                    rightDropdownOpen={this.state.rightDropdownOpen}
                                    sortByLikes={this.state.sortByLikes}
                                    toggleSortLeft={this.toggleSortLeft}
                                    toggleSortRight={this.toggleSortRight}
                                    onListRelatedClick={this.onListRelatedClick}
                                    loadPerspectivesByLikes={this.loadPerspectivesByLikes}
                                    loadByAuthor={this.loadByAuthor}
                                />

                                {this.state.perspectiveLoading &&
                                    <div style={{ position: 'absolute', display: 'flex', justifyContent: 'center', width: '100%' }}>
                                        <img src={loading} style={{ height: '20px' }}></img>
                                    </div>

                                }
                                {
                                   this.state.filteredPerspectives && this.state.filteredPerspectives.length > 0 && this.state.filteredPerspectives.map(data => {
                                        return (
                                            <div>
                                                <Context data={data}
                                                    authors={this.props.concepts.authors}
                                                    selectedOption={this.props.navbar.selectedOption}
                                                    keywords={this.props.concepts.keywords}
                                                    onAuthorClick={this.onAuthorClick}
                                                    onLikeClick={this.onLikeClick}
                                                    isLike={this.state.isLike}

                                                />
                                            </div>
                                        );
                                    })
                                }
                            </div>
                            {this.state.perspectiveLoading &&
                                <div style={{ position: 'absolute', display: 'flex', justifyContent: 'center', width: '100%', bottom: "20" }}>
                                    <img src={loading} style={{ height: '20px' }}></img>
                                </div>

                            }
                            <div style={{ height: '100px' }}>   </div>

                        </div>
                        <div className="col-xs-12 col-sm-12 col-md-2 d-none d-md-block" onScroll={this.handleThirdWheeel} id="thirdScr" style={{ background: 'white', height: '100vh', overflow: 'auto', position: 'fixed', right: 250 }}>
                            <div ref="thirdScroll" id="thirdInnerScr" style={{ borderRadius: "3px", backgroundColor: "white", overflow: [this.state.perspectiveLoading ? 'hidden' : 'auto'] }}>
                                {this.showAuthors && !this.props.sortByLikes && <AuthorsSidebar sortedAuthors={this.props.concepts.sortedAuthors} onAuthorClick={async (author, index) => {
                                    await this.loadPerspectivesByAuthor(author.lastName, index)
                                    this.authorLoading = false

                                }} />}
                            </div>
                        </div>
                        {/* <div className="col-xs-12 col-sm-12 col-md-3" style={{ borderRadius: "3px", backgroundColor: "white", overflow: "auto", borderLeft: "0px", textAlign: "center" }}>
                            <div className="text-center">
                                <AdsComponent></AdsComponent>
                            </div>
                        </div> */}
                    </div>
                    }
                </div>}
            </div>
        );
    }

}
const mapDispatchToProps = (dispatch) => {
    return {
        //getConceptDetails: bindActionCreators(getConceptDetails, dispatch),
        setSelectedItem: bindActionCreators(setSelectedItem, dispatch),
        flushConceptState: bindActionCreators(flushConceptState, dispatch),
        getListRelatedByConceptId: bindActionCreators(getListRelatedByConceptId, dispatch),
        getConceptFromApi: bindActionCreators(getConceptFromApi, dispatch),
        addTag: bindActionCreators(addTag, dispatch),
        increasePerspectiveLike: bindActionCreators(increasePerspectiveLike, dispatch),
        decreasePerspectiveLike: bindActionCreators(decreasePerspectiveLike, dispatch),
        getConceptPerspectivesByKeywords: bindActionCreators(getConceptPerspectivesByKeywords, dispatch),
        getPerspectivesByAuthors: bindActionCreators(getPerspectivesByAuthors, dispatch),
        getConceptPerspectivesByLikes: bindActionCreators(getConceptPerspectivesByLikes, dispatch),

    }
}
const mapStateToProps = (state) => {
    return {
        navbar: state.navbar,
        concepts: state.concepts,
        pageNumber: state.concepts.page_no,
        uniqueKeywords: getUniqueKeywords(state),
        filteredPerspectives: getFilteredPerspectives(state),
        sortByLikes: state.concepts.sortByLikes,
        offset: state.concepts.offset,
        isScrollLike: state.concepts.isScrollByLike
    }
}
