import React, { Component } from 'react';
import loading from '../../../../src/loading.gif';
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import ShareButtons from '../../SocialShareButtons/ShareButtons';

    class PerspectiveColumn extends Component {
        constructor(props)
        {
            super(props);
            this.state={
                loadImage:'none',
                dropdownOpen:false,
                visibility:'none',
                id:'',
                showShare:false
           }
        }
     
    render() {
        let {data,conceptsAuthor}=this.props
        return (
                <div id={data.ConceptId} style={{ marginTop: "10px",marginBottom:'40px'}}>
                    <div className="clearfix" style={{ marginTop: "10px", backgroundColor: "white", padding: "40px", border: "1px solid white", borderRadius: "5px" }} >
                        <b style={{ color: "black", cursor: "pointer", textTransform: 'capitalize' }} onClick={() => this.props.onConceptClick(data)}>{data.pronoun}{" "} {this.props.author.concepts[data.Concept].name}</b>{" is "}{data.description}
                        <div className="text-center" onMouseLeave={() => {this.setState({showShare:false})}}>
                            <i id="shareButton" className="fa fa-send ico " aria-hidden="true" onClick={() => {this.setState({showShare:!this.state.showShare})}} onMouseOver={() => {this.setState({showShare:true})}}>
                            </i> 
                            <span className="contextbutton" onClick={() => {
                                this.setState({
                                    visibility: this.state.visibility == "none" ? "block" : "none",
                                    id:data.id
                                })
                                }} style={{ cursor: "pointer",marginTop:'15px' }}>
                                <span className="fa fa-ellipsis-h ico " id='ellipseCircle' style={{ fontSize: '8px', color: 'black' }}>
                                </span>
                            </span>
                               {/* <span className="contextbutton fa fa-ellipsis-h" id="AuthorEllipseCircle" onClick={() => {
                                    this.setState({
                                        visibility: this.state.visibility == "none" ? "block" : "none",
                                        id: data.id,
                                     })
                                     }} style={{ cursor: "pointer" }}>
                                </span> */}
                                <i style={{ color: data.isLike ? "red" : "rgb(192,192,192)", fontSize: '18px' }} className="fa fa-heart-o " onClick={() => { this.props.onLikeClick(data) }}></i>
                                {this.state.showShare && <div  onMouseLeave={() => {this.setState({showShare:!this.state.showShare})}}  onMouseOver={() => {this.setState({showShare:true})}} style={{background:'#fff',width:"18%",marginLeft:'40%',height:35,display:'flex',alignContent:'center',justifyContent:'center',border:"1px solid #bdbaba",boxShadow:'3px 3px #bdb2b2'}}>
                                 <ShareButtons data={data} title={this.props.author.concepts[data.Concept].name} author={this.props.selectedOption.label}></ShareButtons>
                            
                                </div>}
                        </div>
                        
                        <div id={data.id} style={{ marginTop: "20px", display: [data.id == this.state.id ? this.state.visibility : "none"], textAlign: 'justify' }} >{data.longDescription}<div style={{ backgroundColor: "#DCDCDC" }}>{data.citation}</div></div>
                    </div>
                </div>
  
      );
    }
}
export default PerspectiveColumn