import React, { Component } from 'react';

class ConceptSidebar extends  Component {
    render() {
        console.log("props are",this.props)
      return (
            <div id="firstInnerScr" style={{background:'white'}}> 
                {
                    <ul style={{ width: "150px", margin: "auto",listStyle:'none',lineHeight: "0.9em",height: "auto",listStyle:"none",padding:0 }}>
                        {this.props.uniqueconcepts && this.props.uniqueconcepts.length > 0 && this.props.uniqueconcepts.map((concept,index) => {
                            if (concept.name != undefined) {
                                return <li onClick={() =>this.props.conceptCallBack(index,concept)} style={{ padding:"8px"}}><b><a  style={{ cursor: "pointer", textDecoration: "none", color: "black", display: "block" }}>
                                            {concept.name}<span style={{ color: 'gray',fontSize:11 }}></span></a></b>
                                        </li>
                            }

                        })}
                    </ul>
                }
            </div>

      );
    }
  }
  export default ConceptSidebar