import React, { Component } from 'react';
import ReactDOM from 'react-dom';


class AuthorsSidebar extends Component {

    constructor(props) {
        super(props);
    }
    render() {
        return (
            <ul style={{ "lineHeight": "0.9em", height: "auto", listStyle: "none" }}>
                {this.props.sortedAuthors && this.props.sortedAuthors.length > 0 && this.props.sortedAuthors.map((author, index) => {
                    return (
                        <li style={{ width: "150px", margin: "auto", backgroundColor: "", padding: "8px" }}><a onClick={() => {this.props.onAuthorClick(author,index)}} style={{ color: "black", textDecoration: "none", cursor: "pointer", fontSize: 14, height: "auto" }} href={`#${author.lastName}`}><b>{(author.lastName && author.firstName) ? (author.lastName + ", " + author.firstName) : (author.lastName ? author.lastName : (author.firstName ? author.firstName : ""))}</b></a></li>
                    );
                })}
            </ul>
        );
    }

}

export default AuthorsSidebar;