import React, { Component } from 'react';
import loading from '../../../../src/loading.gif';
import { Link } from "react-router-dom";
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';


    class AuthorDetail extends Component {
        constructor(props)
        {
            super(props);
            this.state={
           }
        }
     
    render() {
        console.log("author is ",this.props)
        return (
            <div style={{ background: 'white', padding: 10, minWidth: '100%' }}>
                <h6>{this.props.dob}-{this.props.dod}</h6>
                <button className="btn btn-link" style={{ color: 'black' }} onClick={() =>  this.props.onBookClick()}><i className="fas fa-book-open"></i> <span><b>({this.props.author.author.Books && this.props.author.author.Books.length})</b></span></button>
                <br /><br />
                {this.props.authorBio && this.props.authorBio.length > 0 && this.props.authorBio.map(item => {
                    return (<div className="" style={{ width: "100%", }}><b style={{ fontSize: 15, display: "block" }}>{item.heading.charAt(0).toUpperCase() + item.heading.slice(1)}:</b>
                        {item.values.map(v => {

                            return <p6 style={{ marginTop: '-10px;' }}><span style={{ display: "inline-block", padding: 0.1 }}><Link style={{ cursor: "pointer", color: 'black', fontSize: 13 }} to="author-clusters" onClick={() => this.props.onBioClick({ distinct_grouping: v.name, distinct_grouping_id: v.id })}>{v.name}</Link>&nbsp;&nbsp;</span></p6>
                        })}

                    </div>);
                })
                }
            </div>
  
      );
    }
}
export default AuthorDetail