import React, { Component } from 'react';
import ShareButtons from '../../SocialShareButtons/ShareButtons';
import './Context.css'
class Context extends Component {
    constructor(props) {
        super(props);
        this.state = {
            visibility: 'none',
            showShare:false,
            showImage:false
        }
    }
    render() {
        
        const { data, authors, keywords } = this.props;
        let keywordId=''
        if(data.Keywords && data.Keywords.length > 0)
        keywordId=keywords[data.Keywords[0]].name
        let authorName=authors[data.Author].lastName+"+"+authors[data.Author].firstName
        let picLink="https://conceptionary-images.s3.amazonaws.com/"+authorName.toLowerCase()+".jpg"
       
    return (
            <div id={keywordId} style={{ marginTop: "10px",marginBottom:'40px'}}  onMouseLeave={() => {this.setState({showShare:false})}}>
            <div style={{border:"1px solid #ececec",width:"100%",borderBottom:'none',height:'40px',paddingLeft:15,paddingTop:9,paddingBottom:'44px'}}>
                <img onError={() => {this.setState({showImage:false})}} onLoad={() => {this.setState({showImage:true})}} style={{width:30,height:30,borderRadius:20,display:this.state.showImage?'inline-block':'none'}} src={picLink} className="rounded-circle"/>
                {!this.state.showImage && <i className="fa fa-user-circle-o" style={{display:'inline',padding:0,fontSize:26}}></i>}
                <b  onClick={() => this.props.onAuthorClick({ author_first: authors[data.Author].firstName, author_last: authors[data.Author].lastName, born: authors[data.Author].dob, died: authors[data.Author].dod, auth_id: data.Author })} style={{ color: "#7b7676", cursor: "pointer",fontSize:'20px' }}> {authors[data.Author].firstName}{" "}{authors[data.Author].lastName}</b></div>
                
            <div id={authors[data.Author].lastName} className="clearfix" style={{ backgroundColor: "white", padding: "25px", border: "1px solid #ececec", textAlign: 'justify' }} >
                <b style={{ fontSize: '18px', textTransform: 'capitalize' }}>{data.pronoun}{" "} {this.props.selectedOption.label}</b>{" is "}{data.description}
                {/* <div onClick={() => this.props.onAuthorClick({ author_first: authors[data.Author].firstName, author_last: authors[data.Author].lastName, born: authors[data.Author].dob, died: authors[data.Author].dod, auth_id: data.Author })} style={{ color: "red", cursor: "pointer" }}><b>{authors[data.Author].firstName}{" "}{authors[data.Author].lastName}</b></div> */}
                <div>   
                   {
                        data.Keywords && data.Keywords.length > 0 && data.Keywords.map(k => {
                            return <div id={keywords[k].name} style={{display:'inline'}}><span id={keywords[k].name} style={{ color: "grey",fontSize:13 }}>{data.Keywords.length > 1 && (keywords[k].name + " | ")}{data.Keywords.length < 2 && (keywords[k].name + " ")}</span></div>
                        })
                    }
                </div>
                <div onMouseLeave={() => {this.setState({showShare:false})}} >
                <div className=""  style={{display:'flex',alignContent:'center',justifyContent:'center',marginTop:'30px'}} >
                     <i id="shareButton" className="fa fa-send ico " aria-hidden="true" onClick={() => {this.setState({showShare:!this.state.showShare})}} onMouseOver={() => {this.setState({showShare:true})}}>
                     </i> 
                    <span className="contextbutton" onClick={() => {
                        this.setState({
                            visibility: this.state.visibility == "none" ? "block" : "none",
                            // id: data.id,
                        })
                        }} style={{ cursor: "pointer",marginTop:'15px' }}>
                        <span className="fa fa-ellipsis-h ico " id='ellipseCircle' style={{ fontSize: '8px', color: 'black' }}>
                        </span>
                    </span>
                         
                        <i style={{ color: data.isLike ? "red" : "rgb(192,192,192)",paddingTop:'18px', fontSize: '18px',cursor:'pointer' }} className="fa fa-heart-o " aria-hidden="true" onClick={() => this.props.onLikeClick(data.id)}></i> 
                       

                </div>
                {this.state.showShare && <div  onMouseLeave={() => {this.setState({showShare:!this.state.showShare})}}  onMouseOver={() => {this.setState({showShare:true})}} style={{background:'#fff',width:"18%",marginLeft:'40%',height:35,display:'flex',alignContent:'center',justifyContent:'center',border:"1px solid #bdbaba",boxShadow:'3px 3px #bdb2b2'}}>
                    <ShareButtons data={data} title={this.props.selectedOption.label} author={authors[data.Author].firstName+" "+authors[data.Author].lastName}></ShareButtons>
                    
                </div>}
                </div> 
                <div id={data.id} style={{ marginTop: "20px", display: [this.state.visibility], backgroundColor: 'center' }} >{this.props.data.longDescription}<div style={{ backgroundColor: "#DCDCDC" }}>{this.props.data.citation}</div></div>

                {/* <div className="text-right" >
                    <i style={{ color: data.isLike ? "red" : "grey",fontSize:'20px' }} className="fa fa-thumbs-o-up " aria-hidden="true" onClick={() => this.props.onLikeClick(data.id)}></i>&nbsp;[{data.loves ? this.props.data.loves : "0"}]&nbsp;&nbsp; <span className="contextbutton" onClick={() => {
                        this.setState({
                            visibility: this.state.visibility == "none" ? "block" : "none",
                            // id: data.id,
                        })
                    }} style={{ cursor: "pointer" }}><i className="fa fa-ellipsis-h" style={{fontSize:'20px'}}></i></span>
                <div id={data.id} style={{ marginTop: "20px", display: [this.state.visibility] }} >{this.props.data.longDescription}<div style={{ backgroundColor: "#DCDCDC" }}>{this.props.data.citation}</div></div>
                
                </div>
                
                <div>
                    <ShareButtons id={data.id}></ShareButtons>
                </div> */}
            </div>
            </div>
        );
    }
}
// Context.PropTypes = {
//     data: PropTypes.object.isRequired,
//     selectedOption: PropTypes.object.isRequired,
//     onAuthorClick: PropTypes.func.isRequired,
// }
export default Context;