import React, { Component } from 'react';
import loading from '../../../loading.gif';
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import { FaSortAmountUp } from 'react-icons/fa';
class Header extends  Component {
    constructor(props)
    {
        super(props);
        this.state={
            loadImage:'none',
            dropdownOpen:false,
            visibility:'none',
            id:''
        }
        this.toggleSort = this.toggleSort.bind(this);
    }
    
    toggleSort() {
        this.setState(prevState => ({
            dropdownOpen: !prevState.dropdownOpen
        }))
    }
    render() {
      console.log("Header props are",this.props)
      
      return (
          <div id="authorspace" style={{ background: 'rgb(246,250,252)', position: 'fixed', height: '20px', width:519, top: 47 }}>
                <div id="authorheader" className="" style={{ zIndex: '4', position: 'fixed', width: 422, backgroundColor: '#fff', border: '1px solid #b7b7b7', boxShadow: '3px 3px #a99e9e' }}>
                    <div className="row" >
                        <div className="col-md-2 col-2 col-sm-2" style={{ padding:'5px  0 0 25px' }}>
                            <img src={this.props.author.pictureLink} onError={() => { this.setState({ loadImage: "none" }) }} onLoad={() => { this.setState({ loadImage: "block" }); }} alt={this.props.navbar.selectedOption.label} style={{ width: "60%", height: "90%", display: this.state.loadImage, borderRadius: '100%' }} />
                        </div>
                        <div className="col-8 col-md-8 col-sm-8" style={{ top: '-3px' }}><b style={{ fontSize: '175%' }}>{this.props.navbar.selectedOption.label}</b></div>
                        <div className="col-2 col-md-2 col-sm-2">
                            <div className="dropdown">
                                <Dropdown isOpen={this.state.dropdownOpen} toggle={this.toggleSort} style={{ boxSahdow: '0 0 0 white', outline: 'none' }}>
                                    <DropdownToggle left style={{ background: 'none', border: 'none', color: 'black',paddingTop:4 }}>
                                        <FaSortAmountUp id="sortIcon" />
                                    </DropdownToggle>
                                    <DropdownMenu id="authorDropDown">
                                        <DropdownItem onClick={this.props.sortByLike}>Likes</DropdownItem>
                                        <DropdownItem onClick={this.props.sortByName}>Name</DropdownItem>
                                    </DropdownMenu>
                                </Dropdown>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

      );
    }
  }
  export default Header