import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import sort from '../../../images/sort.png'
import { FaSortAmountUp } from 'react-icons/fa';

class Header extends Component {

    constructor(props) {
        super(props);
    }

    render() {
        
        console.log("list related data is ",this.props)
        return (
            <div id="header" className="" style={{ zIndex: '4', position: 'fixed', backgroundColor: '#fff', border: '1px solid #b7b7b7', boxShadow: '3px 3px #a99e9e' }}>
                <div className="row" >
                    <div className="col-md-2 col-2 col-sm-2">
                        <div className="dropdown">

                            <Dropdown isOpen={this.props.leftDropdownOpen} toggle={this.props.toggleSortLeft}>
                                <DropdownToggle style={{ background: 'none', border: 'none', color: 'black', boxShadow: ' 0 0 0 0 white' }}>
                                    <i className="fa fa-list ico" style={{lineHeight:'24px', padding: '10px', fontSize: '20px', color: '#6f6b6b' }}></i>
                                </DropdownToggle>
                                <DropdownMenu >
                                   {this.props.listRelatedData && returnOnlyListData(this.props.listRelatedData).length > 0 && returnOnlyListData(this.props.listRelatedData).map(data => {
                                        return <DropdownItem className="list-group-item" style={{ fontSize: 16, fontWeight: "500", color: 'black', cursor: 'pointer' }} onClick={() => this.onListRelatedClick({ relationary_supra: data.name, relationary_thing_supra_id: data.id })}>
                                            {data.name}
                                        </DropdownItem>
                                    })}
                                    {this.props.listRelatedData && returnOnlyListData(this.props.listRelatedData).length < 1 &&
                                        <DropdownItem className="list-group-item" style={{ fontSize: 16, fontWeight: "500", color: 'gray', cursor: 'pointer' }} >
                                            not in any lists yet
                                        </DropdownItem>

                                    }

                                </DropdownMenu>
                            </Dropdown>

                        </div>
                    </div>

                    <div className="col-8 col-md-8 col-sm-8" style={{ top: '-3px' }}><b style={{ fontSize: '30px',lineHeight:'52px' }}>{this.props.selectedOption.label}</b></div>
                    <div className="col-2 col-md-2 col-sm-2">
                        <div className="dropdown">
                            <Dropdown isOpen={this.props.rightDropdownOpen} toggle={this.props.toggleSortRight}>
                                <DropdownToggle style={{ background: 'none', border: 'none', color: 'black', boxShadow: ' 0 0 0 0 white',lineHeight:'2' }} color='none'>
                                    {/* <img src={sort} className="img-responsive" style={{ width: "60%", marginTop: 7 }} /> */}
                                    <FaSortAmountUp id="sortIcon" />
                                    
                                </DropdownToggle>
                                <DropdownMenu  style={{left:'-270% !important'}}>
                                    <DropdownItem className="list-group-item" onClick={this.props.loadPerspectivesByLikes} style={{ color: this.props.sortByLikes ? 'black' : 'gray', cursor: 'pointer' }}>by popularity</DropdownItem>
                                    <DropdownItem className="list-group-item" onClick={this.props.loadByAuthor} style={{ color: this.props.sortByLikes ? 'black' : 'gray', cursor: 'pointer' }}>by author</DropdownItem>
                                </DropdownMenu>
                            </Dropdown>

                        </div>
                    </div>
                </div>
            </div>
        );
    }

}

const returnOnlyListData = (listRelatedData) => {
    let listData = [];
    listRelatedData.forEach(element => {
        if (element.type == 'list of') {
            listData.push(element);
        }
    })
    return listData;
}


export default Header;